import React from 'react';
import Link from 'gatsby-link';
import CSSModules from 'react-css-modules';
import styles from './SiteNav.module.css';

const SiteNav = () => (
  <nav styleName="base">
    <Link to="/lab">Lab</Link>
    <Link to="/">Home</Link>
  </nav>
);

export default CSSModules(SiteNav, styles);



