import React from 'react';
import Helmet from 'react-helmet';

const PageHead = () => (
  <Helmet
    title="Undistractions"
    meta={[
      {
        name: 'description',
        content:
          'Undistraction is the website of Pedr Browne, a designer and frontend developer based in Newcastle, UK',
      },
      {
        name: 'keywords',
        content:
          'javaScript, css, front-end developer, programmer, web-developer, designer',
      },
    ]}
  >
    <link
      type="text/css"
      rel="stylesheet"
      href="//fast.fonts.net/cssapi/916b72d7-b5ee-4d1c-b36f-4a0c548a5331.css"
    />
  </Helmet>
);

export default PageHead;



